import React, { useState, useEffect } from 'react';
import { recruiterAPI } from '../services/apiService';
import '../index.css';
import '../recruiter.css';

const CreateJobPage = ({ onBack, onJobCreated, recruiterId }) => {
  console.log('CreateJobPage rendered with props:', { onBack, onJobCreated, recruiterId });
  
  // Check if recruiterId is valid
  if (!recruiterId) {
    console.error('CreateJobPage: recruiterId is missing or invalid');
    return (
      <div className="error-container">
        <div className="error-message">
          <i className="fas fa-exclamation-triangle"></i>
          <h3>Error</h3>
          <p>Recruiter ID is missing. Please go back and try again.</p>
          <button className="btn btn-primary" onClick={onBack}>
            Go Back
          </button>
        </div>
      </div>
    );
  }
  // Define districts and fields options
  const districts = [
    'Northern District',
    'Haifa District', 
    'Central District',
    'Tel Aviv District',
    'Jerusalem District',
    'Southern District'
  ];


  const fields = [
    'Software Development', 'Data Science', 'Cybersecurity', 'DevOps', 'UI/UX Design',
    'Product Management', 'Marketing', 'Sales', 'Finance', 'Human Resources',
    'Operations', 'Customer Success', 'Business Development', 'Content Creation',
    'Graphic Design', 'Digital Marketing', 'Project Management', 'Quality Assurance',
    'System Administration', 'Network Engineering', 'Mobile Development', 'Web Development',
    'Game Development', 'Artificial Intelligence', 'Machine Learning', 'Cloud Computing',
    'Database Administration', 'Technical Writing', 'Research & Development', 'Consulting'
  ];

  const degreeOptions = [
    'No Degree',
    'Bachelor\'s Degree',
    'Master\'s Degree',
    'PhD or Doctorate',
    'College Diploma',
    'Professional Certification',
    'Bootcamp Graduate',
    'High School Diploma'
  ];

  // Load data from localStorage on component mount
  const loadSavedData = () => {
    const savedData = localStorage.getItem('createJobFormData');
    if (savedData) {
      try {
        return JSON.parse(savedData);
      } catch (error) {
        console.error('Error parsing saved form data:', error);
        return null;
      }
    }
    return null;
  };

  const [jobData, setJobData] = useState(() => {
    const savedData = loadSavedData();
    return savedData || {
      title: '',
      description: '',
      company: '',
      status: 'Active',
      qualifications: [],
      ticket: {
        location: '',
        experience: '',
        remote: 'On-Site',
        jobType: 'Full-time',
        field: '',
        degree: ''
      }
    };
  });

  const [errors, setErrors] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');
  const [newQualification, setNewQualification] = useState('');

  // Save data to localStorage whenever jobData changes
  useEffect(() => {
    localStorage.setItem('createJobFormData', JSON.stringify(jobData));
  }, [jobData]);

  // Note: We don't clean up localStorage on page unload
  // because we want to preserve data on refresh
  // Data will only be cleared when user explicitly cancels or creates job successfully

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    
    if (name.startsWith('ticket.')) {
      const ticketField = name.split('.')[1];
      setJobData(prev => ({
        ...prev,
        ticket: {
          ...prev.ticket,
          [ticketField]: value
        }
      }));
    } else {
      setJobData(prev => ({
        ...prev,
        [name]: value
      }));
    }
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleAddQualification = () => {
    if (newQualification.trim() && !jobData.qualifications.includes(newQualification.trim())) {
      setJobData(prev => ({
        ...prev,
        qualifications: [...prev.qualifications, newQualification.trim()]
      }));
      setNewQualification('');
    }
  };

  const handleRemoveQualification = (index) => {
    setJobData(prev => ({
      ...prev,
      qualifications: prev.qualifications.filter((_, i) => i !== index)
    }));
  };

  const handleQualificationKeyPress = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleAddQualification();
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!jobData.title.trim()) {
      newErrors.title = 'Job title is required';
    }
    
    if (!jobData.description.trim()) {
      newErrors.description = 'Job description is required';
    }
    
    if (!jobData.company.trim()) {
      newErrors.company = 'Company name is required';
    }
    
    if (!jobData.ticket.location.trim()) {
      newErrors['ticket.location'] = 'Location is required';
    }
    
    if (!jobData.ticket.field.trim()) {
      newErrors['ticket.field'] = 'Field is required';
    }
    
    if (!jobData.ticket.experience.trim()) {
      newErrors['ticket.experience'] = 'Experience is required';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSave = async () => {
    if (!validateForm()) {
      return;
    }
    
    setIsLoading(true);
    setSuccessMessage('');
    
    try {
      const jobToSave = {
        ...jobData,
        recruiterId: parseInt(recruiterId)
      };
      
      const result = await recruiterAPI.createJob(jobToSave);
      
      if (result.success) {
        setSuccessMessage('Job created successfully!');
        // Clear localStorage and reset form
        localStorage.removeItem('createJobFormData');
        setJobData({
          title: '',
          description: '',
          company: '',
          status: 'Active',
          qualifications: [],
          ticket: {
            location: '',
            experience: '',
            remote: 'On-Site',
            jobType: 'Full-time',
            field: '',
            degree: ''
          }
        });
        setErrors({});
        
        // Auto redirect immediately after successful job creation
        localStorage.removeItem('isOnCreateJobPage');
        // Use the onJobCreated prop instead of direct navigation
        if (onJobCreated) {
          onJobCreated();
        }
      } else {
        setErrors({ general: result.message || 'Failed to create job' });
      }
    } catch (error) {
      console.error('Failed to create job:', error);
      setErrors({ general: 'Failed to create job' });
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    // Clear localStorage when canceling
    localStorage.removeItem('createJobFormData');
    localStorage.removeItem('isOnCreateJobPage');
    // Use the onBack prop instead of direct navigation
    if (onBack) {
      onBack();
    }
  };

  console.log('CreateJobPage: About to render component');
  
  return (
    <div className="create-job-page" style={{ backgroundColor: 'white', minHeight: '100vh', padding: '20px' }}>
      {/* Debug info */}
      <div style={{ backgroundColor: 'red', color: 'white', padding: '10px', marginBottom: '20px' }}>
        DEBUG: CreateJobPage is rendering! RecruiterId: {recruiterId}
      </div>
      
      <div className="page-header">
        <button className="back-btn" onClick={handleCancel}>
          <i className="fas fa-arrow-left"></i> Go Back to Portal
        </button>
        <h1>Create New Job Posting</h1>
      </div>

      <div className="page-content">
        {successMessage && (
          <div className="success-message">
            <i className="fas fa-check-circle"></i>
            {successMessage}
          </div>
        )}

        {errors.general && (
          <div className="error-message">
            <i className="fas fa-exclamation-triangle"></i>
            {errors.general}
          </div>
        )}

        <div className="auto-save-indicator">
          <i className="fas fa-save"></i>
          <span>Your progress is automatically saved</span>
        </div>

        <div className="form-container">
          <div className="form-section">
            <h2>Job Information</h2>
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Job Title *</label>
                <input
                  type="text"
                  className={`form-input ${errors.title ? 'error' : ''}`}
                  name="title"
                  value={jobData.title}
                  onChange={handleInputChange}
                  placeholder="Enter job title"
                />
                {errors.title && <span className="error-message">{errors.title}</span>}
              </div>
              
              <div className="form-group">
                <label className="form-label">Company *</label>
                <input
                  type="text"
                  className={`form-input ${errors.company ? 'error' : ''}`}
                  name="company"
                  value={jobData.company}
                  onChange={handleInputChange}
                  placeholder="Enter company name"
                />
                {errors.company && <span className="error-message">{errors.company}</span>}
              </div>
            </div>
            
            <div className="form-group">
              <label className="form-label">Job Description *</label>
              <textarea
                className={`form-textarea ${errors.description ? 'error' : ''}`}
                name="description"
                value={jobData.description}
                onChange={handleInputChange}
                placeholder="Describe the job position, responsibilities, and requirements"
                rows="6"
              />
              {errors.description && <span className="error-message">{errors.description}</span>}
            </div>
            
            <div className="form-group">
              <label className="form-label">Job Status</label>
              <select
                className="form-select"
                name="status"
                value={jobData.status}
                onChange={handleInputChange}
              >
                <option value="Active">ACTIVE</option>
                <option value="Closed">NOT ACTIVE</option>
              </select>
            </div>
          </div>
          
          <div className="form-section">
            <h2>Job Requirements</h2>
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Location *</label>
                <select
                  className={`form-select ${errors['ticket.location'] ? 'error' : ''}`}
                  name="ticket.location"
                  value={jobData.ticket.location}
                  onChange={handleInputChange}
                >
                  <option value="">Select District</option>
                  {districts.map(district => (
                    <option key={district} value={district}>{district}</option>
                  ))}
                </select>
                {errors['ticket.location'] && <span className="error-message">{errors['ticket.location']}</span>}
              </div>
              
              <div className="form-group">
                <label className="form-label">Experience (Years) *</label>
                <input
                  type="number"
                  className={`form-select ${errors['ticket.experience'] ? 'error' : ''}`}
                  name="ticket.experience"
                  value={jobData.ticket.experience}
                  onChange={handleInputChange}
                  min="0"
                  max="100"
                  required
                />
                {errors['ticket.experience'] && <span className="error-message">{errors['ticket.experience']}</span>}
              </div>
            </div>
            
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Remote Work</label>
                <select
                  className="form-select"
                  name="ticket.remote"
                  value={jobData.ticket.remote}
                  onChange={handleInputChange}
                >
                  <option value="On-Site">On-Site</option>
                  <option value="Remote">Remote</option>
                  <option value="Hybrid">Hybrid</option>
                </select>
              </div>
              
              <div className="form-group">
                <label className="form-label">Job Type</label>
                <select
                  className="form-select"
                  name="ticket.jobType"
                  value={jobData.ticket.jobType}
                  onChange={handleInputChange}
                >
                  <option value="Full-time">Full-time</option>
                  <option value="Part-time">Part-time</option>
                  <option value="Contract">Contract</option>
                  <option value="Freelance">Freelance</option>
                </select>
              </div>
            </div>
            
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Field *</label>
                <select
                  className={`form-select ${errors['ticket.field'] ? 'error' : ''}`}
                  name="ticket.field"
                  value={jobData.ticket.field}
                  onChange={handleInputChange}
                >
                  <option value="">Select Field</option>
                  {fields.map(field => (
                    <option key={field} value={field}>{field}</option>
                  ))}
                </select>
                {errors['ticket.field'] && <span className="error-message">{errors['ticket.field']}</span>}
              </div>
              
              <div className="form-group">
                <label className="form-label">Degree</label>
                <select
                  className="form-select"
                  name="ticket.degree"
                  value={jobData.ticket.degree}
                  onChange={handleInputChange}
                >
                  <option value="">Select Degree</option>
                  {degreeOptions.map(degree => (
                    <option key={degree} value={degree}>{degree}</option>
                  ))}
                </select>
              </div>
            </div>
          </div>

          <div className="form-section">
            <h2>Job Qualifications</h2>
            <div className="form-group">
              <label className="form-label">Required Qualifications</label>
              <div className="qualifications-input">
                <input
                  type="text"
                  className="form-input"
                  value={newQualification}
                  onChange={(e) => setNewQualification(e.target.value)}
                  onKeyPress={handleQualificationKeyPress}
                  placeholder="Enter a qualification requirement"
                />
                <button
                  type="button"
                  className="btn btn-secondary btn-sm"
                  onClick={handleAddQualification}
                  disabled={!newQualification.trim()}
                >
                  <i className="fas fa-plus"></i> Add
                </button>
              </div>
              
              {jobData.qualifications.length > 0 && (
                <div className="qualifications-list">
                  <h4>Current Qualifications:</h4>
                  <ul className="qualifications-tags">
                    {jobData.qualifications.map((qualification, index) => (
                      <li key={index} className="qualification-tag">
                        <span>{qualification}</span>
                        <button
                          type="button"
                          className="remove-qualification"
                          onClick={() => handleRemoveQualification(index)}
                        >
                          <i className="fas fa-times"></i>
                        </button>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>

        <div className="form-actions">
          <button className="btn btn-secondary" onClick={handleCancel} disabled={isLoading}>
            <i className="fas fa-times"></i> Cancel
          </button>
          <button className="btn btn-primary" onClick={handleSave} disabled={isLoading}>
            {isLoading ? (
              <>
                <i className="fas fa-spinner fa-spin"></i> Creating...
              </>
            ) : (
              <>
                <i className="fas fa-plus"></i> Create Job
              </>
            )}
          </button>
        </div>
      </div>
    </div>
  );
};

export default CreateJobPage;
